  color: black;
  font-family: 'Source Serif Pro', serif;
  font-size: 1em;
  font-weight: bold;
}

.searchbar:focus {
  width: 400px;
  font-family: 'Source Serif Pro', serif;
  font-size: 1em;
  font-weight: bold;
  transition: 0.3s;
  border-bottom: black solid 2px;
  outline: none;
}

